<template>
  <v-container fluid fill-height>
    <v-row>
      <v-dialog
        v-model="dialog.show"
        max-width="500px"
      >
        <v-card>
          <v-card-title>
            <span class="headline">Component code</span>
          </v-card-title>
          <v-card-text>
            <pre>
              <code>
              {{ dialog.code }}
              </code>
            </pre>
            <hr>
            <h3>Vulnerability</h3>
            <p>
              {{ dialog.description }}
            </p>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-col cols="6" class="mx-auto">
        <br /><br />
        <v-row class="text-center">
          <v-text-field
            v-model="bearer_token"
            label="FL0 Bearer Token"
            type="text"
            prepend-icon="mdi-lock"
            @change="refresh_org_list"
          ></v-text-field>
        </v-row>
        <v-row>
          <v-select
            v-model="selected_org"
            :items="organizations"
            label="Organization"
            item-text="name"
            item-value="id"
            @change="fetchProjects"
          >
            <!-- if loading -->
            <template v-slot:selection="" v-if="is_org_loading">
              <v-progress-circular
                indeterminate
                color="primary"
                size="16"
              ></v-progress-circular>
            </template>
          </v-select>
        </v-row>
      </v-col>
    </v-row>
    <div class="d-flex align-center justify-center" style="width: 100%">
      <v-btn class="success" v-if="selected_project" @click="scanProject">
        <template v-if="is_vulns_loading">
          <v-progress-circular indeterminate color="primary" size="16"></v-progress-circular>
        </template>
        <template v-else>
          Scan Project
        </template>
      </v-btn>
    </div>
    <v-row style="width: 100%; height: 70vh">
      <v-col cols="3">
        <h3>Projects</h3>
        <template v-if="is_proj_loading">
          <v-progress-linear
            indeterminate
            color="primary"
            class="mb-0"
          ></v-progress-linear>
        </template>
        <template v-else>
          <br />
          <hr />
          <br />
          <div v-for="(project, index) in projects" :key="index">
            <v-btn
              text
              small
              :class="
                selected_project && project.id === selected_project.id
                  ? 'primary'
                  : ''
              "
              width="100%"
              @click="selectProject(project)"
            >
              {{ project.name }}
            </v-btn>
            <br />
            <br />
          </div>
          <template v-if="projects.length === 0">
            <v-alert :value="true" type="info" border="left" class="ma-0">
              Please select an organization.
            </v-alert>
          </template>
        </template>
      </v-col>
      <v-col cols="4">
        <h3>Dependencies</h3>
        <br />
        <hr />
        <br />
        <v-data-table :headers="headers" :items="items">
          <template slot="no-data">
            <template v-if="is_deps_loading">
              <v-progress-linear
                indeterminate
                color="primary"
              ></v-progress-linear>
            </template>
            <template v-else-if="selected_project">
              <v-alert
                :value="true"
                type="info"
                icon="mdi-information"
                border="left"
                class="ma-0"
              >
                No dependencies found.
              </v-alert>
            </template>
            <template v-else>
              <v-alert :value="true" type="info" border="left" class="ma-0">
                Please select a project.
              </v-alert>
            </template>
          </template>
        </v-data-table>
      </v-col>
      <v-col>
        <h3>Scan Results</h3>
        <br />
        <hr />
        <br />
        <div>
          <v-card v-if="!project_scanned">
            <v-alert :value="true" type="info" border="left" class="ma-0">
              Please select a project and click Scan Project.
            </v-alert>
          </v-card>
          <template v-else-if="is_vulns_loading">
            <v-progress-linear
              indeterminate
              color="primary"
            ></v-progress-linear>
          </template>
          <template v-else-if="vulns.length > 0 || code_vulns.length > 0">
            <v-card v-if="code_vulns.length > 0">
              <v-card-title>
                <span class="subheading">Code Vulnerabilities found</span>
              </v-card-title>
              <v-card-text>
                <v-data-table
                  dense
                  disable-pagination
                  hide-default-footer
                  :headers="code_vuln_headers"
                  :items="code_vulns"
                  @click:row="showDialog"
                >
                </v-data-table>
              </v-card-text>
            </v-card>
            <hr />
            <v-card v-if="vulns.length > 0">
              <v-card-title>
                <span class="subheading">Vulnerabilities found</span>
              </v-card-title>
              <v-card-text>
                <v-data-table
                  dense
                  disable-pagination
                  hide-default-footer
                  :headers="vuln_headers"
                  :items="vulns"
                >
                </v-data-table>
              </v-card-text>
            </v-card>
            
          </template>
          <v-card v-else-if="vulns_done_loading">
            <v-card-title>
              <span class="subheading">No vulnerabilities found</span>
            </v-card-title>
          </v-card>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";

const buffer = require("buffer");

export default {
  name: "HomeView",

  components: {},

  data() {
    return {
      bearer_token: "",
      workspace_id: "",
      selected_org: null,
      organizations: [],
      projects: [],
      selected_project: null,
      project_scanned: false,
      is_org_loading: false,
      is_deps_loading: false,
      is_proj_loading: false,
      is_vulns_loading: false,
      vulns_done_loading: true,
      dialog: {
        show: false,
        code: "",
        description: "",
      },
      vulns: [],
      vuln_headers: [
        {
          text: "Package",
          value: "package",
        },
        {
          text: "Severity",
          value: "severity",
        },
        {
          text: "Title",
          value: "title",
        },
        {
          text: "Fixed in",
          value: "fixedIn",
        },
      ],
      code_vulns: [],
      code_vuln_headers: [
        {
          text: "Vulnerability",
          value: "type",
        },
        {
          text: "Description",
          value: "description",
        }
      ],
      headers: [
        {
          value: "name",
          text: "Name",
        },
        {
          value: "version",
          text: "Version",
        },
      ],
      items: [],
    };
  },

  mounted() {
    this.bearer_token = localStorage.getItem("bearer_token");
    this.refresh_org_list();
  },

  methods: {
    refresh_org_list() {
      this.is_org_loading = true;
      axios
        .get(`https://portal.fl0.com/api/me`, {
          headers: {
            Authorization: `${this.bearer_token}`,
          },
        })
        .then((response) => {
          localStorage.setItem("bearer_token", this.bearer_token);
          this.organizations = response.data.organizations;
          this.is_org_loading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    fetchProjects() {
      if (this.selected_org) {
        this.is_proj_loading = true;
        axios
          .get(
            `https://portal.fl0.com/api/me/organizations/${this.selected_org}/workspaces`,
            {
              headers: {
                Authorization: `${this.bearer_token}`,
              },
            }
          )
          .then((response) => {
            this.projects = response.data.workspaces;
            this.is_proj_loading = false;
          })
          .catch((error) => {
            console.log(error);
            this.is_proj_loading = false;
          });
      }
    },
    selectProject(project) {
      this.selected_project = project;
      this.fetchDependencies();
      this.project_scanned = false;
    },
    fetchDependencies() {
      if (this.selected_project) {
        this.is_deps_loading = true;
        axios
          .get(
            `https://portal.fl0.com/api/workspaces/${this.selected_project.id}/nodeServer`,
            {
              headers: {
                Authorization: `${this.bearer_token}`,
              },
            }
          )
          .then((response) => {
            this.items = response.data.npmPackages;
            this.is_deps_loading = false;
          })
          .catch((error) => {
            this.is_deps_loading = false;
            console.log(error);
          });
      }
    },
    scanProject() {
      if (this.selected_project) {
        this.is_vulns_loading = true;
        this.vulns_done_loading = false;
        this.fetchNodeJsCodes().then(codes=>{
          codes.forEach((code) => {
            this.fetchVulns(code).then(vulns=>{
              vulns.code = code;
              this.code_vulns = this.code_vulns.concat(vulns);
            });
          })
        }).finally(() => {
          this.is_vulns_loading = false;
          this.project_scanned = true;
        });
        this.items.forEach((item) => {
          axios
            .get(
              `https://api.fl0.com/eliastest/uat/snyktest/flows/scan?name=${item.name}&version=${item.version}`
            )
            .then((response) => {
              let mapped_response = response.data.map((vuln) => {
                return {
                  package: `${vuln.name}@${vuln.version}`,
                  severity: vuln.severityWithCritical,
                  title: vuln.title,
                  fixedIn: vuln.fixedIn ? vuln.fixedIn : "",
                };
              });
              this.vulns = this.vulns.concat(mapped_response);
              this.is_vulns_loading = false;
            })
            .catch((error) => {
              console.log(error);
            })
            .finally(() => {
              this.vulns_done_loading = true;
            });
        });
      }
    },
    async fetchNodeJsCodes() {
      let flows = await this.fetchFlows();
      let node_js_codes = [];
      flows.forEach((flow) => {
        flow.steps.forEach((step) => {
          if (step.parameters) {
            step.parameters.forEach((parameter) => {
              if (parameter.key === 'script'){
                node_js_codes.push(parameter.expression);    
              }
            });
          }
        });
      });
      console.log(node_js_codes);
      return node_js_codes;
    },
    async fetchFlows() {
      let workspace_id = this.selected_project.id;
      return axios
        .get(
          `https://portal.fl0.com/api/workspaces/${workspace_id}/resourceTree`,
          {
            headers: {
              Authorization: `${this.bearer_token}`,
            },
          }
        )
        .then(async (response) => {
          let folders = []
          await Promise.all(
            response.data.folders.map(async (folder) => {
              return Promise.all(
                folder.resources.map(async (endpoint) => {
                  let flow = await this.fetchFlowFromEndpoint(endpoint.id);
                  if(flow){
                    folders.push(flow);
                  }
                })
              );
            })
          );
          return folders
        })
        .catch((error) => {
          console.log("Error fetching resources", error);
          return [];
        });
    },
    async fetchFlowFromEndpoint(endpoint_id) {
      let workspace_id = this.selected_project.id;
      return axios
        .get(
          `https://portal.fl0.com/api/workspaces/${workspace_id}/flows/${endpoint_id}`,
          {
            headers: {
              Authorization: `${this.bearer_token}`,
            },
          }
        )
        .then((response) => {
          return response.data;
        })
        .catch((error) => {
          console.log("Error fetching flow", error);
          return false;
        });
    },
    async fetchVulns(code) {
      return axios
        .post(
          `https://api.fl0.com/eliastest/uat/snyktest/flows/scan-js-code`,
          {
            code_content: buffer.Buffer.from(code).toString("base64"),
          }
        )
        .then((response) => {
          console.log("Response", response);
          let vulns = response.data.map((vuln) => {
            if(vuln){
              return {
                type: vuln.ruleId,
                description: vuln.message.text,
                code: code,
              };
            }
          });
          return vulns
        })
        .catch((error) => {
          console.log(error);
          return [];
        });
    },
    showDialog(code_vuln) {
      console.log("Clicked", code_vuln);
      this.dialog.show = true;
      this.dialog.code = code_vuln.code;
      this.dialog.description = code_vuln.description;
    },
  },

  watch: {
    bearer_token: function (val) {
      localStorage.setItem("bearer_token", val);
    },
    workspace_id: function (val) {
      localStorage.setItem("workspace_id", val);
    },
  },
};
</script>
